* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(238, 238, 238);
  font-family: monospace;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
  padding: 1em;
  grid-gap: 1em;
  gap: 1em;
  min-height: calc(100vh - 80px - 60px);
}

.pane {
  flex: 1 1;
}

.text-input {
  resize: vertical;
  width: 100%;
  background-color: #Fff;
  border-radius: 4px;
  border: 1px solid rgb(228, 228, 228);
  font-size: 1em;
  min-height: 2em;
}

.page-header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  background-color: rgb(42, 230, 230);
  color: #fff;
  font-weight: bold;
  box-shadow: 1px 1px 15px 1px rgba(0,0,0,0.10);
}

.page-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  background-color: rgb(14, 153, 153);
  color: #fff;
}

.sub-option {
  padding-left: 2.5em;
  padding-bottom: 0.1em;
}

@media only screen and (min-width: 1500px) {
  .container {
    width: 80%;
  }
}

@media only screen and (min-width: 992px) {
  .container {
    flex-direction: row;
    margin-top: 80px;
  }

  .page-header {
    position: fixed;
    top: 0;
  }
}
.card {
  background-color: #fff;
  box-shadow: 1px 1px 15px 1px rgba(0,0,0,0.10);
  border-radius: 4px;
  padding: 0.5em;
}
.checkbox-container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 0.5em;
  gap: 0.5em;
  cursor: pointer;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  height: 20px;
  width: 20px;
  background-color: #eee;
}

.checkbox-container input ~ .checkmark {
  border-radius: 4px;
}

.checkbox-container:hover input ~ .checkmark {
  background-color: #ccc;
}

.checkbox-container input:checked ~ .checkmark {
  background-color: rgb(42, 230, 230);
}

.checkbox-container input:checked:hover:enabled ~ .checkmark {
  background-color: rgb(39, 214, 214);
}

.checkbox-container input:disabled ~ .checkmark {
  background-color: rgb(180, 180, 180);
  cursor: not-allowed;
}

.checkmark:after {
  content: "";
  position: relative;
  left: 6px;
  top: 2px;
  display: none;
}

.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

.checkbox-container .checkmark:after {
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.checkbox-container input:focus ~ .checkmark{
  outline: rgb(0, 110, 255) 2px solid;
}
.collapsible-card {
  background-color: #fff;
  border-radius: 4px;
  padding: 1em;
  margin: 1em 0;
  box-shadow: 1px 1px 15px 1px rgba(0,0,0,0.10);
  height: auto;
  overflow: hidden;
}

.collapsible-card .titlebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-bottom: 15px;
}

.collapsible-card .titlebar .title-holder {
  flex: 1 1;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.collapsible-card .titlebar .actions .collapse-btn {
  width: 25px;
  height: 25px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 1.2em;
  color: rgb(100, 100, 100);
}

.collapsible-card .titlebar .actions .collapse-btn .collapse-icon{
  transition: all 200ms;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.collapsible-card .titlebar .actions .collapse-btn .collapsed-icon{
  transition: all 200ms;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}


.rule-container {
  border: 1px solid rgb(230, 230, 230);
  border-radius: 4px;
  padding: 0.5em;
  display: flex;
  flex-direction: column;
  grid-gap: 0.5em;
  gap: 0.5em;
  width: 100%;
}

.rule-container .row {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  
}

.rule-container .row-right {
  justify-content: flex-end;
}

.rule-container input {
  flex: 1 1;
  height: 2em;
}

.rule-container .row select {
  height: 2em;
}

.rule-container .row button {
  padding: 2px 5px;
}

.rules-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.rules-container .add-rule-btn {
  margin: 0 1em;
  width: 100px;
}

.rule-container .info-text {
  margin-left: 1em;
  color: rgb(136, 136, 136);
}
